@font-face {
  font-family: 'Montserrat';
  font-display: swap;
  src: url('../fonts/Montserrat/MontserratRegular/Montserrat-Regular.eot');
  src: url('../fonts/Montserrat/MontserratRegular/Montserrat-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Montserrat/MontserratRegular/Montserrat-Regular.woff') format('woff'),
  url('../fonts/Montserrat/MontserratRegular/Montserrat-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

